import uniPopup from "../../components/uni-popup/uni-popup.vue"
export default {
  components: {uniPopup},
	data() {
		return {
			aaaaa:'',
			showcode:1,
			numbertime:59,
			time:null,
			type:'center',
			authentication:false
		}
	},
	methods: {
		asdasd(){
			console.log('我失焦了')
		},
		
		//获取验证码
		getcode(){
			this.authentication = true
			this.showcode = 1
			this.time = setInterval(()=>{
				this.numbertime--
				if(this.numbertime <= 0){
				this.showcode = 2
				this.numbertime = 59
					clearInterval(this.time)
				}
			},1000)
		},
		
		
		//关闭错误验证码弹窗
		cancel(){
			this.authentication = false
		}
	},
	mounted() {
		this.getcode()
	},
	destroyed() {
		if(this.time){
			clearInterval(this.time)
		}
	},
	watch:{
		aaaaa(news,olds){
			console.log(news.length)
		}
	}
}
